(function() {
  'use strict';

  angular
    .module('assign', ['ngSanitize', 'ngAria', 'ui.router', 'toastr', 'oc.modal']);

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .controller('registerModalController', registerModalController);

  /** @ngInject */
  function registerModalController($scope, $ocModal) {

    $scope.submitAadharNumber = function() {
      $scope.aadharSubmited = true;
    }

    $scope.submitaadharOtp = function() {
      $scope.aadharOtpSubmited = true;
      $ocModal.close();
    }

	}

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .directive('numbersOnly', numbersOnly);

  /** @ngInject */
  function numbersOnly() {
    return {
      require: 'ngModel',
      link: function (scope, element, attr, ngModelCtrl) {
        function fromUser(text) {
          if (text) {
            var transformedInput = text.replace(/[^0-9]/g, '');

            if (transformedInput !== text) {
              ngModelCtrl.$setViewValue(transformedInput);
              ngModelCtrl.$render();
            }
            return transformedInput;
          }
          return undefined;
        }            
        ngModelCtrl.$parsers.push(fromUser);
        }
    };
  }

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($ocModal, $log, toastr, $timeout) {
    var vm = this;

    // vm.pdfName = 'Relativity: The Special and General Theory by Albert Einstein';
    // vm.pdfUrl = 'assets/pdf/relativity.pdf';
    // vm.pdfPassword = 'test';
    // vm.scroll = 0;
    // vm.loading = 'loading';
    vm.aadharSubmitted = false;
    vm.signing = false;
    vm.signed = false;

    vm.getNavStyle = function(scroll) {
      if(scroll > 100) return 'pdf-controls fixed';
      else return 'pdf-controls';
    }

    vm.onLoad = function() {
      vm.loading = '';
    }

    vm.showAModal = function() {

      $ocModal.open({
        url: 'partials/modal.html',
        closeOnEsc: false,
        controller: 'registerModalController',
        onClose: function() {
          vm.aadharSubmitted = true;
          vm.signing = true;
          $timeout(function(){
            vm.signing = false;
            vm.signed = true;
          }, 3000);
          toastr.success('Aadhar verified successfully');
        }
      });
    };



    vm.showAModal();
  }
})();

(function() {
  'use strict';

  angular
    .module('assign')
    .factory('LoginService', LoginService);

  /** @ngInject */
  function LoginService() {

    var userInfo = {
      emailId: '',
      SID: null
    };

    this.getEmailId = function() {
      return userInfo.emailId;
    };

    this.setEmailId = function(emailId) {
      userInfo.emailId = emailId;
    };

    return this;
  }
})();

(function() {
  'use strict';

  angular
    .module('assign')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(LoginService, $state) {
    var vm = this;

    vm.submitLogin = function(mailId) {
      LoginService.setEmailId(mailId);
      $state.go('home');
    }


  }
})();

(function() {
  'use strict';

  angular
    .module('assign')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $state) {

		$rootScope.$on('$stateChangeError', function(event, toState) { 
			// console.log(event, toState, toParams, fromState, fromParams);
			if (toState.url === '/login') {
				$state.go('home');
			} else if (toState.url === '/') {
				$state.go('login')
			}
		});
  }

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm',
        resolve: {
          loginCheck: function(LoginService, $q) {
            var deferred = $q.defer();
            if (LoginService.getEmailId() !== '') {
              deferred.resolve();
            }
            deferred.reject();
            return deferred.promise;
          }
        }
      })
       .state('login', {
        url: '/login',
        templateUrl: 'app/Login/login.html',
        controller: 'LoginController',
        controllerAs: 'Login',
        resolve: {
          loginCheck: function(LoginService, $q) {
            var deferred = $q.defer();
            if (LoginService.getEmailId() === '') {
              deferred.resolve();
            }
            deferred.reject();
            return deferred.promise;
          }
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('assign')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

